import Layout from "../../components/Layout"
import IconButton from "../../components/IconButton"
import {IoMdAdd} from "@react-icons/all-files/io/IoMdAdd"
import { useEffect, useReducer } from "react"
import Form from "./Form"
import BgLoader from "../../components/BgLoader"
import { useDispatch, useSelector } from "react-redux"
import toast from "react-hot-toast"
import {get} from "../../application/middlewares/index.js"
import Table from "../../components/Table/Table"
import Head from "../../components/Table/Head"
import Row from "../../components/Table/Row"
import Cell from "../../components/Table/Cell"
import Body from "../../components/Table/Body"
import NoContent from "../../components/NoContent"
import {BiEdit} from "@react-icons/all-files/bi/BiEdit"
import {BsImage} from "@react-icons/all-files/bs/BsImage"
import {BiImageAdd} from "@react-icons/all-files/bi/BiImageAdd"


function reducer (state, action) {
    switch(action.type){
        case "ADD_FORM":
            return {
                ...state,
                visible:true,
                form:"add",
                title:"Добавить производителя"
            }
        case "SET_CLOSE_MODAL":
            return {
                ...state,
                visible:false
            }
        case "SET_DATA":
            return{
                ...state,
                loading:false,
                data:action.payload
            }
        case "SET_IMAGE_FORM":
            return{
                ...state,
                visible:true,
                form:"image_upload",
                values:action.payload,
                title:"Изменить фотографию"
            }
        case "SET_UPLOADED_IMAGE":
            return {
                ...state,
                data:state.data.map(item => {
                    if(item.id === action.payload.id){
                        item.destination = action.payload.destination
                    }return item
                }),
                visible:false
            }
        case "ADD_PRODUCER":
            return {
                ...state,
                data:[...state.data, action.payload]
            }
        default: return state
    }
}

function Producers (){
    const [state, setState] = useReducer(reducer, {
        visible:false, form:"", data:[], loading:false, title:""
    })
    const token = useSelector(state => state.auth.token)
    const dispatch = useDispatch()
    useEffect(()=>{
        dispatch(get({
            url:`api/admin/producers`,
            token,
            action : (response) =>{
                if(response.success){
                    console.log(response.data.rows)
                    setState({type:"SET_DATA", payload:response.data.rows})
                }else{
                    toast.error("Something went wrong")
                }
            }
        }))
    }, []) // eslint-disable-line react-hooks/exhaustive-deps

    return (
        <Layout header = {<Header handleClick = {()=> setState({type:"ADD_FORM", payload:{}})}/>}>
            <Form
                visible = {state.visible}
                token = {token}
                values = {state.values}
                form = {state.form}
                setCloseModal = {()=>setState({type:"SET_CLOSE_MODAL", payload:{}})}
                title = {state.title}
                setUploadedImage = {(value)=>setState({type:"SET_UPLOADED_IMAGE", payload:value})}
                addProducer = {(value) => setState({type:"ADD_PRODUCER", payload:value})}
            />
            <BgLoader loading = {state.loading} />
            <div className="w-full h-full px-6 overflow-y-auto pb-20 ">
                <Table>
                    <Head>
                        <>
                        <Row>
                            <Cell>
                                <div  className={` px-2 py-4 font-medium flex flex-row justify-start items-center `}>
                                    Номер
                                </div>
                            </Cell>
                            <Cell>
                                <div  className={` px-2 py-4 font-medium flex flex-row justify-start items-center `}>
                                    Производитель
                                </div>
                            </Cell>
                            <Cell>
                                <div  className={` px-2 py-4 font-medium flex flex-row justify-center items-center `}>
                                    Фотография
                                </div>
                            </Cell>
                            <Cell>
                                <div  className={` px-2 py-4 font-medium flex flex-row justify-start items-center `}>
                                    Действие
                                </div>
                            </Cell>
                        </Row>
                        </>
                    </Head>
                    <Body>
                        {state.data.length > 0
                        ?
                            state.data.map(item => (
                                <Row key = {item.id}>
                                    <>
                                    <Cell>
                                        <div className = "p-2">
                                            {item?.id}
                                        </div>
                                    </Cell>
                                    <Cell>
                                        <div className = "p-2">
                                            {item?.name}
                                        </div>
                                    </Cell>
                                    <Cell>
                                        <div className="p-2 flex justify-center items-center">
                                            {item?.destination ? <img className="object-contain rounded-md w-28 h-28" src={`${process.env.REACT_APP_FILE_URL}/${item?.destination}-big.webp`} alt="producer_image" />: <BsImage className="text-4xl text-gray-500 opacity-80"/>}
                                        </div>
                                    </Cell>
                                    <Cell>
                                        <div className = "p-2 flex flex-row">
                                            <IconButton tooltip = {"Изменить"} handleClick = {()=>setState({type:"SET_UPDATE_FORM", payload:{...item}})} icon = {<BiEdit className = "text-2xl"/>}/>
                                            <IconButton tooltip = {"Изменить фотографию"} handleClick = {()=>setState({type:"SET_IMAGE_FORM", payload:item})} icon = {<BiImageAdd className = "text-2xl"/>}/>
                                        </div>
                                    </Cell>
                                </>
                                </Row>
                            ))
                        :
                        <tr>
                            <td colSpan="10" >
                                <div className="flex w-full h-full py-20 justify-center items-center">
                                    <NoContent title="Нет производителей"/>
                                </div>
                            </td>
                        </tr>
                        }
                    </Body>
                </Table>
            </div>
        </Layout>
    )
}

const Header = ({handleClick}) =>(
    <div className="flex flex-row justify-between items-center">
        <IconButton tooltip = {"Добавить производителя"}  handleClick={handleClick} icon={<IoMdAdd className="text-2xl "/>}/>
        {/* <div className="w-80">
            <SearchInput action={(value) => handleSearch(value)} placeholder="Поиск"/>
        </div> */}
    </div>
);
export default Producers