import { useForm } from "react-hook-form";
import ModalContainer from "../../components/ModalContainer";
import * as Yup from "yup"
import { yupResolver } from "@hookform/resolvers/yup";
import { useDispatch } from "react-redux";
import { post } from "../../application/middlewares";
import toast from "react-hot-toast";
import ImageUpload from "../../components/ImageUpload";
import { useState } from "react";
import BgLoader from "../../components/BgLoader";

function Form({visible, title, setCloseModal, form, addProducer, token, values, setUploadedImage}){
    const {register,  handleSubmit, formState: { errors }, reset} = useForm({
        resolver:yupResolver(schema(form))
    })
    console.log(values)
    const dispatch = useDispatch()
    const [loading, setLoading] = useState(false)
    const onSubmit = (data)=>{
        setLoading(true)
        if(form === "add"){
            dispatch(post({
                url:"api/admin/add-producer",
                data,
                token,
                action: (response) =>{
                    if(response.success){
                        toast.success("Hey yu have added")
                        console.log(response.data.rows);
                        setLoading(true)
                        setCloseModal() 
                        addProducer(response.data.rows)
                    }else{
                        toast.error("Something went wrong")
                    }
                }
            }))
        }
        setLoading(false)

    }

    return (
        <ModalContainer size="md" 
            setCloseModal={() => {
                setCloseModal();
                reset({enabled:false})
            }} 
            visible={visible} 
            title={title}
        >
            {form === "add" ? 
            <form onSubmit = {handleSubmit(onSubmit)}>
                <div className="relative w-full mb-6">
                    <label>Введите название бренда</label>
                    <input type="text" {...register("name")}
                        autoComplete="false"
                        className={`${errors.name ? 'border-2 border-red-300 ring-red-100' : 'ring-indigo-600'} px-3 shadow-inner h-10 w-full text-base bg-gray-50 rounded-md z-20 focus:bg-white focus:outline-none focus:ring-2 `}
                        placeholder="Max-Deluxe"
                    />
                    <p className="absolute bottom-0 left-0 -mb-4 text-xs font-medium text-red-400">
                        {errors.name?.message}
                    </p>
                </div>
                <div className="w-full flex justify-center items-center absolute -bottom-6">
                    <button type="submit" disabled = {loading}  className="w-50 flex remove-button-bg justify-center items-center px-4 h-10 text-white transform ease-in-out duration-300 hover:scale-110 active:scale-100 font-semibold text-base rounded-full bg-green-500 hover:bg-green-400 active:bg-green-500 focus:outline-none shadow-md">
                        {loading 
                            ?
                            <BgLoader loading = {loading}/>
                            :
                            <div>{form === "update" ? "Изменить" : "Добавить"}</div>
                        }
                    </button>
                </div>
            </form>
            :
            form === "image_upload" ?
            <div className = "w-96">
                <ImageUpload 
                    visible={true}
                    myDocument={values}
                    token={token}
                    setUploadedImage={setUploadedImage}
                    url={`/api/admin/add-producer-image/${values?.id}`}
                />
            </div>
            :
            <div>
            </div>
            }

        </ModalContainer>
    )
}

const schema = (form) => Yup.object().shape({
    name: form === "image_upload" ? Yup.string().nullable(true) : Yup.string().min(3, "Минимум 3 символа").max(50, "Максимум 50 символа")
})

export default Form;